h3{
	margin: 10px;
}






.table{
	border: 1px solid #8f8f8f;
	table-layout: fixed;
	width: 100%;
	margin-bottom: 20px;
}

.table th {
	font-weight: bold;
	padding: 5px;
	background: #1b1f28;
	border: 1px solid #8f8f8f;
}
.table td{
	padding: 5px 10px;
	border: 1px solid #8f8f8f;
	text-align: left;
}
.table tbody tr:nth-child(odd){
	background: #281b23;
}
.table tbody tr:nth-child(even){
	background: #1b1f28;
}



